import React, { ReactElement, FormEvent, useState, useCallback, useMemo, useEffect, createRef } from 'react';
import CSV from 'papaparse';
import url from 'url';
import { Player, TournamentPlayers, ID, TournamentStandings, TournamentSettings } from '../interfaces';
import { GET, POST, server, FORM} from '../utils/requests';

import styles from '../css/sidebar.module.css';
import { AiFillEdit, AiOutlineClose, AiOutlineCheck } from 'react-icons/ai';
import { MdRefresh, MdMenu } from 'react-icons/md';
import { IoMdClose } from 'react-icons/io';
import { Bottom } from '../components/Buttons';
import CollapsableSection, { SectionBreak } from '../components/CollapsableSection';
import TabSection from '../components/TabSection';

interface SidebarProps {
	playerDict: Map<ID, Player>
	playerDictKey: string //required to force a reload
	updatePlayerDict: (force?: boolean) => void
    setPlayerDict: (value: {
        id?: string;
        player?: Player;
        players?: Player[];
        hash?: string;
    }) => void
	id: string
	settings: TournamentSettings
	setDisplayPlayer: (id: string) => void
    checkIfEnoughPlayers: (reload?: boolean) => void
    togglePasteInput: () => void
}

interface ActiveProps {
	player: Player

	editPlayer: (e: React.MouseEvent<HTMLButtonElement, MouseEvent>) => void
	activatePlayer: (e: React.MouseEvent<HTMLButtonElement, MouseEvent>) => void
    clearPlayer: (e: React.MouseEvent<HTMLButtonElement, MouseEvent>) => void
    allowNewPlayers: boolean
    setDisplayPlayer: (id: string) => void

	round: number
	requireLastName: boolean
}

export function ActivePlayer(props: ActiveProps): ReactElement {

	const displayName = props.requireLastName ?
		[props.player.firstName, props.player.lastName].join(' ') :
		props.player.firstName;
	
	return (
		<div id={props.player.id} className={styles.playerLine}>
			<div
				className={['playerInfo', styles.playerInfo, !props.player.active ? styles.playerInactive : ''].join(' ')}
				style={{
					gridColumn: props.round && !props.allowNewPlayers ? '1 / span 3' : undefined
				}}
				onClick={(e: React.MouseEvent<HTMLDivElement>) => props.setDisplayPlayer((e.target as HTMLDivElement).parentElement.id)}
			>
				{displayName} ({props.player.rating.toString()}) {props.round && (props.player.played < props.round) ? '*' : ''}
			</div>
			{props.round && !props.allowNewPlayers ? null : <>
				<button name='edit' className={styles.edit} onClick={(props.editPlayer)} >
					<AiFillEdit />
				</button>
				{props.player.active ?
					<button name='cancel' className={styles.cancel} onClick={(props.clearPlayer)}>
						<AiOutlineClose />
					</button> :
					<button name='active' className={styles.activate} onClick={(props.activatePlayer)}>
						<AiOutlineCheck />
					</button>
				}
			</>}
		</div>
	);

}

interface FormProps {
	id: string
	player?: Player,
	requireLastName: boolean
	onSubmit: (e: React.FormEvent<HTMLFormElement>) => Promise<void>
	onKeyDown?: (e: React.KeyboardEvent<HTMLFormElement>) => void
    handlePaste?: (e: React.ClipboardEvent<HTMLInputElement>) => void
    action: string
}

export function EditableForm(props: FormProps) {

    const firstInput = createRef() as React.RefObject<HTMLInputElement>;
    const [focus, mustFocus] = useState(false);

    useEffect(() => {
        if (!focus && !props.player) return;
        if (!firstInput.current) return;
        firstInput.current.focus();
    }, [firstInput, props.player])
	
	return (
		<div className={styles.scoreSection}>
			<form
				target='_self'
				onKeyDown={props.onKeyDown}
				onSubmit={(e) => props.onSubmit(e).then(() => mustFocus(true))}
				method='POST'
				action={props.action}
			>
                <input type='text' className={styles.input} name='firstName' required style={{width: props.requireLastName ? '25%' : '65%'}}
                    ref={firstInput}
					placeholder={props.player ? undefined : props.requireLastName ? 'First name' : 'Team name'}
					defaultValue={props.player?.firstName}
					onPaste={props.handlePaste}
				/>
				<input type='text' className={styles.input} name='lastName' required={props.requireLastName} style={{width: '40%', display: props.requireLastName ? 'block' : 'none'}}
					placeholder={props.player ? undefined : 'Last name'}
					defaultValue={props.player?.lastName} 
				/>
				<input type='number' className={styles.input} min={0} max={3200} name='rating' required style={{width: '20%'}}
					placeholder={props.player ? undefined : props.requireLastName ? 'Rating' : 'Av. Rating'}
					defaultValue={props.player?.rating}
				/>
				<input type='hidden' name='id' value={props.player?.id} required/>
				<button tabIndex={-1} className={styles.submit}>
					<AiOutlineCheck />
				</button>
			</form>
		</div>
	);
}

export default function Sidebar(props: SidebarProps): ReactElement {

	const [round, setRound] = useState(0);
	const [players, setPlayersRaw] = useState({} as TournamentPlayers);
	const [linkedState, setLinkedState] = useState({} as TournamentStandings);

	/** UPDATES SECTION */

	const updatePlayerState = useCallback((): Promise<TournamentStandings> => {
		if (!props.id) return Promise.resolve({} as TournamentStandings);
		return GET({ url: '/tournament/' + props.id + '/getState' })
			.then(({round, players}: {round: number, players: TournamentStandings}) => {
				setLinkedState(players);
				setRound(round);
				return players;
			});
	}, [props.id]);

	/**
	 * The fundamental method. Called everytime there's a change to the player list. The backbone of this element.
	 */
	const setPlayers = useCallback(async (refresh?: boolean | void): Promise<void> => {
		let state = refresh !== false ? await updatePlayerState() : linkedState;
		if (!state) return Promise.resolve();
		let players = {} as TournamentPlayers;
		for (let [k, v] of Object.entries(state)) {
			players[k] = v.map((id: string) => {
				let player = props.playerDict.get(id);
				//if (!player) console.error('Unmatched ID: ' + id);
				return player;
			}).sort((a: Player, b: Player) => b.rating - a.rating).filter((v) => v);
		}
		setPlayersRaw(players);
		props.checkIfEnoughPlayers(false);
	}, [updatePlayerState, props.playerDict, props.checkIfEnoughPlayers, linkedState]);

	/** CALLBACK SECTION */
	
	const updatePlayer = useCallback((id: ID): Promise<Player> => {
		return GET({
			url: '/tournament/' + props.id + '/fetchPlayer',
			params: { id }
		})
			.then((player: Player) => {
				props.setPlayerDict({player});
				return player;
			});
	}, [props.id, props.setPlayerDict]);

	const activatePlayer = useCallback((e: React.MouseEvent<HTMLButtonElement, MouseEvent>): void => {
		let button = e.target as HTMLButtonElement;
		if (button.constructor.name !== 'HTMLButtonElement') return;
        let id = (button.parentNode as HTMLDivElement).id;
		POST({
			url: '/tournament/' + props.id + '/activatePlayer',
			data: { id }
		})
			.then(() => updatePlayer(id))
			.catch(console.error);
	}, [props.id, updatePlayer]);

	/**
	 * Clears a single page. Called when the 'X' button is hit next to a player
	 */
	const clearPlayer = useCallback((e: React.MouseEvent<HTMLButtonElement, MouseEvent>): void => {
		let button = e.target as HTMLButtonElement;
		if (button.constructor.name !== 'HTMLButtonElement') return;
		let id = (button.parentNode as HTMLDivElement).id;
		POST({
			url: '/tournament/' + props.id + '/clearPlayer',
			data: { id }
		})
			.then((data: string) => {
                if (data === 'inactive') updatePlayer(id);
                else props.setPlayerDict({id});
            })
			.catch(console.error);
	}, [updatePlayer, props.id]);

	/**
	 * Clears all players. Called when the 'X' button is pressed at the bottom of the sidebar
	 */
	const clearPlayerList = useCallback(async (): Promise<void> => {
		await POST({ url: '/tournament/' + props.id + '/clearPlayers' }).catch(console.error);
		props.checkIfEnoughPlayers();
	}, [props.id, props.checkIfEnoughPlayers]);
	
	const requireLastName = useMemo(() => props.settings.competitors === 'individual', [props.settings.competitors]);

	/* RENDERING SECTION */

	const handlePaste = useCallback((e: React.ClipboardEvent<HTMLInputElement>) => {
		let text = e.clipboardData.getData('text').toString();
		if (!text.trim()) return;
		if (!text.split('\n').length && text.split(',').length && text.split('\t').length) return;
		e.preventDefault();
		let json = CSV.parse(text, {
			delimitersToGuess: [',', '\t'],
			dynamicTyping: true,
			fastMode: true
		} as CSV.ParseConfig) as CSV.ParseResult;
		//if (json.data.length > 100) return console.error('Can\'t paste more than 100 players at once!');
		let data = json.data.map((row) => {
			let [firstName, lastName, rating]: [string, string, number] = row;
			if (!requireLastName) [firstName, rating, lastName] = [...row, ''];
			if (!firstName || (!lastName && requireLastName) || rating === null || rating === undefined) return null;
			try {
				if (typeof firstName !== 'string') throw [firstName, 'string'].join(',');
				if (typeof lastName !== 'string' && requireLastName) throw [lastName, 'string'].join(',');
				if (typeof rating !== 'number') throw [rating, 'number'].join(',');
				return {firstName, lastName, rating};
			} catch (error) {
				if (!text.split('\n').length) return null;
				let e = error.split(',');
				console.error(new TypeError('Bad input on row ' + row.join(',') + '\n' + e[0] + ' is not type ' + e[1]));
				return null;
			}
		})
			.filter(r => r);
		POST({
			url: 'tournament/' + props.id + '/appendPlayers',
			data
		})
			.then(() => Promise.all([props.updatePlayerDict(), setPlayers()]));
	}, [props.id, requireLastName, props.updatePlayerDict, setPlayers]);

	const Forms = useMemo((): JSX.Element[] => {

		let forms = [] as JSX.Element[];
		
		for (let i = 0; i < Math.max(1, 10 - Object.values(players).reduce((acc: number, curr: Player[]) => acc += curr.length, 0)); i++) {
			forms.push(
				<EditableForm
					key={['newPlayerForm', i].join('.')}
					id={props.id}
					player={null}
					requireLastName={requireLastName}
					onSubmit={(e: FormEvent<HTMLFormElement>): Promise<void> => FORM(e)
						.then(() => {
							let form = e.target as HTMLFormElement;
							form.reset();
						})
                        .then(() => props.updatePlayerDict())
                    }
                    handlePaste={i ? undefined : handlePaste}
                    action={url.resolve(server, ['tournament', props.id, 'appendPlayer'].join('/'))}
				/>
			);
		}
		return forms;
    }, [props.updatePlayerDict, props.id, requireLastName, players, handlePaste]);
    
    function Entry(entryProps: { player: Player }): ReactElement {

        const [edit, setEdit] = useState(false);
        const [player, setPlayer] = useState(entryProps.player);

        if (edit) return <EditableForm
            id={props.id}
            requireLastName={requireLastName}
            player={player}
            onKeyDown={(e: React.KeyboardEvent<HTMLFormElement>) => {
                const ESCAPE_KEY = 27;
                if (e.keyCode !== ESCAPE_KEY) return;
                setEdit(false);
            }}
            onSubmit={async (e: FormEvent<HTMLFormElement>): Promise<void> => FORM(e)
                .then(async () => {
                    let newPlayer = await updatePlayer(player.id);
                    setPlayer(newPlayer);
                    setEdit(false);
                })
            }
            action={url.resolve(server, ['tournament', props.id, 'editPlayer'].join('/'))}
        />

        return <ActivePlayer
            player={player}
            round={round}
            requireLastName={requireLastName}
            editPlayer={() => setEdit(true)}
            activatePlayer={activatePlayer}
            clearPlayer={clearPlayer}
            allowNewPlayers={props.settings.allowNewPlayers}
            setDisplayPlayer={props.setDisplayPlayer}
        />
    }

	const List = useMemo((): ReactElement[] => {
		let list = [] as ReactElement[];
		for (let i = round; i >= 0; i-= 0.5) {
			let score = i.toString();
            if (!players[score] || !players[score].length) continue;
            let section = <CollapsableSection
                key={['sidebar', 'round', score].join('.')}
                header={score + ' points'}
				useInternalState={true}
				expanded={false}
                id={['container', score].join('.')}
            >
                {players[score].map((p) => <Entry key={p.id} player={p} />)}	
            </CollapsableSection>
            list.push(section);
		}
		return list;
	}, [round, players]);

    useEffect(() => {
		if (!props.playerDict.size) return;
        setPlayers(false);
    }, [props.playerDict, setPlayers]);


    useEffect(() => {
		if (!props.playerDict.size) return;
        setPlayers();
    }, [props.playerDict, props.playerDictKey]);
	return (		
        <>	
            <div className={styles.sidebar}>
				<TabSection
					name={'tournamentPlayers'}
					setName={() => {}}
					nameDict={{
						tournamentPlayers: 'Tournament Players'
					}}
				/>
                <div className={['scrollable', styles.playersSection].join(' ')}>
                    {!List.length ? <div className={styles.scoreSection}>
                        <div className={styles.playerLine}>
                            <div className={styles.sectionHeader}>
                                {List.length ? 'Player List' :'No players added yet'}
                            </div>
                        </div>
                    </div> : null}
                    {List}
                    {round && !props.settings.allowNewPlayers ? null :
                        <div className={styles.scoreSection}>
                            {Forms}
                        </div>
                    }
                    <SectionBreak />
                </div>
            </div>
            <div className={['bottomBanner', styles.bottomBanner].join(' ')}>
                {Bottom(
                    [async () => {
                        props.togglePasteInput();
                    }, <MdMenu />, 'Paste'],
                    [async () => {
                        props.updatePlayerDict(true);
                    }, <MdRefresh />, 'Refresh'],
                    round === 0 ? [async () => { /*
                        for (let form of Object.values(this.refs)) {
                            if (form.constructor.name !== 'HTMLFormElement') continue;
                            (form as HTMLFormElement).reset();
                        }*/
                        if (round !== 0) return;
                        await clearPlayerList();
                        props.updatePlayerDict();
                        setPlayers();
                    }, <IoMdClose />, 'Close'] : null
                )}
            </div>
        </>
	);
};